							next = function() {
								var sel = fm.selected(),
									key = $.ui.keyCode[(!sel.length || fm.cwdHash2Elm(sel[0]).next('[id]').length)? 'RIGHT' : 'HOME'];
								$(document).trigger($.Event('keydown', { keyCode: key, ctrlKey : false, shiftKey : false, altKey : false, metaKey : false }));
							},
							val;
						if (code >= 96 && code <= 105) {
							code -= 48;
						}
